@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');


* {
  font-family: 'Source Code Pro', monospace;
  background-color: #001a33;
  color: #a8b2d1;
  /* #64ffda */
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
}

.profile {
  /* text-align: center; */
  max-width: 80%;
  margin: 30px auto;
  padding: 20px;
  margin-top: 100px;
  
}

.navbar {
  padding: 20px;
  /* display: flex; */
  align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  position: fixed;
  z-index: 3;
  top: 0;
  width: 100%;
  padding-left: 500px;
}

.navbar .links {
  margin-left: auto;
}

.navbar .links a {
  margin-left: 80px;
  text-decoration: none;
  padding: 6px;
  font-weight: bold;
  color: #ccd6f6;
}

.navbar .links a:hover {
  color: #64ffda;
}

.profile button {
  padding: 15px;
  width: auto;
  border: 1px solid white;
  font-size: large;
}

.profile button:hover {
  background-color: #64ffda;
  opacity: 0.5;
  color: #001a33;
  border: 1px solid #001a33;
  font-weight: bold;
}

.info, .skills, .outer {
  content: "";
  display: table;
  clear: both;
  
}

.info div, .skills div, .outer div {
  float: left;
  width: auto;
  padding: 0 20px;
}

#intro {
  margin-top: 60px;
  text-align: left;
}

#intro h1, h4 {
  color: whitesmoke;
  margin: 0;
}

#intro p, .about h4 {
  color: #64ffda;
  margin: 0;
}

.about {
  margin-left: auto;
  max-width: 60%;
}

.about h3 {
  color: #64ffda;
}

ul {
  list-style: none;
  margin-top: 5px;
}

.skills ul li::before, .expdiv ul li::before {
  content: "\2022";
  color: #64ffda;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

li {
  color: #ccd6f6;
}

.social {
  margin-top: 30px;
  text-align: center;
}

.social a {
  text-decoration: none;
}

.social a:hover {
  color: #64ffda;
}

.foot {
  font-size: small;
  text-align: center;
  color: slategrey;
}

.education {
  margin-top: 80px;
  margin-left: 15%;
  margin-bottom: 80px;
}

.education h1 {
  color: whitesmoke;
  margin-bottom: 60px;
}

.edudiv {
  padding: 15px;
  margin-left: 70px;
  margin-top: 20px;
  margin-bottom: 20px;
  max-width: 60%;
  box-shadow: 1px 3px 5px black;
}

.edudiv h3 {
  color: #64ffda;
}

.deg {
  color: whitesmoke;
  font-style: italic;
}

.experience {
  margin-top: 80px;
  margin-left: 15%;
  margin-bottom: 80px;
}

.experience h1 {
  color: whitesmoke;
  margin-bottom: 20px;
}

.expdiv {
  padding: 15px;
  margin-left: 70px;
  margin-top: 20px;
  margin-bottom: 20px;
  max-width: 70%;
  box-shadow: 1px 3px 5px black;
}

.expdiv h3 {
  color: #64ffda;
}

.projects {
  margin-top: 80px;
  margin-left: 15%;
  margin-bottom: 80px;
  margin-right: 15%;
  
}

.projects h1 {
  color: whitesmoke;
  margin-bottom: 60px;
}

.projdiv {
  margin-left: 50px;
  height: 250px;
  text-align: left;
  box-shadow: 1px 3px 5px black;
  border-radius: 20px;
}

.projdiv h3 {
  color: #64ffda;
}

.gitproj {
  text-align: right;
}

.projdiv a {
  display: inline-block;
  text-decoration: none;
  font-size: larger;
  color: #f2f2f2;
  transition: 0.5s;
}

.projdiv a:hover {
  color: #64ffda;
  transform: scale(2,2);
}

.techdiv ul {
  list-style-type: square;
}

.techdiv ul li::before {
  content: "\25AA";
  color: #64ffda;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1.2em;
}

.carousel.carousel-slider, .slider-wrapper {
  height: 300px; 
} 
